import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NotificationService } from 'src/app/modules/main/services/notification.service';
import { IUserLogin, User } from 'src/app/shared/interfaces/i-auth';
import { AuthService } from '../../services/auth.service';
import { environment } from 'src/environments/environment';
import Ws from "@adonisjs/websocket-client";

@Component({
  selector: 'app-login',
  templateUrl: './waitphone.component.html',
  styleUrls: ['./waitphone.component.scss']
})
export class WaitPhoneComponent implements OnInit {
// test: string
ws: any;
loginChanel: any;
email!:string
password!:string
user!: User
userData!: IUserLogin

logInForm: FormGroup;
hide = true;
constructor(private authService: AuthService, 
            private router: Router, 
            private formB: FormBuilder,
            private notiSvc: NotificationService) {
  this.buildForm();
}

ngOnInit(): void {
  var data = localStorage.getItem('user');
  this.user = JSON.parse(data);
  console.log(this.user);

  this.ws = Ws(`${environment.WS_URL}`, {
    path:"ws"
  })

  this.ws.connect();

  this.loginChanel = this.ws.subscribe('access:4')

  this.loginChanel.on("message", (data:any) => {
    console.log(data)
  })

  this.loginChanel.emit("message", "hello message")
}

ngOnDestroy(): void {
  this.ws.disconnect();
  this.ws.close();
  localStorage.removeItem('user');
}

  logIn(): void{
    /*if(this.logInForm.invalid){
      this.notiSvc.openSnackBar("Favor de introducir los datos correctamente", 2000)
      return ;
    }
    this.setAuthData()
    this.authService.onLogIn(this.userData).subscribe( () => {
        console.log("pog")
        this.router.navigate(['panel/empresas']);
      }, error => {
        console.log(error);
        this.notiSvc.openSnackBar("Credenciales Incorrectas", 2000)
      });*/
  }

  register(): void{
      this.router.navigate(['auth/login']);
  }

  buildForm(): void {
    this.logInForm = this.formB.group({
      code: ['', Validators.required]
    });
  }

  setAuthData(): void{
    this.userData = {
      email: this.logInForm.get('email')?.value,
      password: this.logInForm.get('password')?.value
    };
  }
}
